export interface IConfig {

    /**
     * Preferences
     * 
     * @type {any}
     * 
     * @memberof IConfig
     */
    prefs: IDictionary<any>;

    type: ConfigType;

    isOpener: boolean;
}

export interface IDictionary<T> {
    [Key: string]: T;
}

export enum ConfigType {
    OpenConfig = 'openConfig',
    ApplyConfig = 'applyConfig'
}
